import { validate } from './validate';

class Validate {

  constructor() {

  }

  static install(Vue) {
    Vue.directive('blue-validate', {
      bind(elm, binding) {
        validate.call(this, {
          elm,
          binding
        });
      }
    });

    Vue.prototype.$validate = function ($event) {
      const elm = $event.target;

    };
  }
}

export default Validate;


